.content {
    padding: 0;
    margin: 40px 200px 0 200px;
}
@media screen and (max-width: 1150px) {
    .content {
        margin: 40px auto 0;
        width: 90%;
    }
}
@media screen and (max-width: 768px) {
    .content {
        margin: 42px auto 0 auto;
        width: 88.125%;
    }
}
